const size = 15;

export const defaultGrid = () => {
	const grid = [];
	for (let i = 0; i < size; i++) {
		const row = [];
		for (let j = 0; j < size; j++) {
			const cell = { id: `${i}_${j}`, value: '.', color: '' };
			row.push(cell);
		}
		grid.push(row);
	}
	return grid
}

export const defaults = {
	grid: defaultGrid(),
	width: size,
	height: size,
	scale: 15,
	color: {
		id: '1_1',
		value: '1',
		color: '#9d9d9d'
	},
	contrast: true
}

